:root {
    --color-primary: #2584ff;
    --color-secondary: #00d9ff;
    --color-accent: #ff3400;
    --color-headings:#1b0760;
    --color-body: #918Ca4;
    --color-body-darker: #5c5577;
    --color-border: #ccc;
    --border-radius: 30px;
}
/*topography*/
html {
    font-size: 62.5%;
}
*, *::after, *::before {
    box-sizing: border-box;
}
body {
    font-family: inter, Arial, Helvetica, sans-serif;
    color: var(--color-body);
    font-size: 2.4rem;
    line-height: 1.5;
}
h1,
h2,
h3 {
    color: var(--color-headings);
    margin-bottom: 2rem;
    line-height:1.1;
}
h1 {
    font-size: 7rem;
}
h2 {
    font-size: 4rem;
}
h3 {
    font-size: 3rem;
}
p {
    margin-top: 0;
}
@media screen and (min-width: 1024px) {
    body {
        font-size: 1.8rem;
    }
    h1 {
        font-size: 8rem;
    }
    h2 {
        font-size: 4rem;
    }
    h3 {
        font-size: 2.4rem;
    }
}

/*links*/
a {
    text-decoration: none;
}
.link-arrow {
    color: var(--color-accent);
    font-weight: bold;
    font-size: 2rem;
    text-transform: uppercase;
}
.link-arrow::after {
    content:'-->';
    margin-left: 5px;
    transition: margin 0.15s;
}
.link-arrow:hover::after {
    margin-left: 10px;
}
@media screen and (min-width: 1024px) {
    .link-arrow {
        font-size: 1.5rem;
    }
}

/*badges*/

.badges {
    padding: 0.5rem 2rem;
    border-radius: 20px;
    font-weight: 600;
    white-space: nowrap;
    font-size: 2rem;
}
.badges--primary {
    background: var(--color-primary);
    color: #fff;
}
.badges--secondary {
    background: var(--color-secondary);
    color: #fff;
}
.badges--small {
    font-size: 1.6rem;
}
@media screen and (min-width: 1024px) {
    .badges {
        font-size: 1.5rem;
    }
    .badges--small {
        font-size: 1.2rem;
    }
}

/*list*/
.list {
    color: var(--color-headings);
    list-style: none;
    padding-left: 0;
}
.list--inline .list__item {
    display: inline-block;
    margin-left: 2rem;
}
.list--tick {
    list-style-image: url(/images/tick.svg);
    padding-left: 3rem;
}
.list--tick .list__item {
    padding-left: 0.5rem;
    margin-bottom: 1rem;
}

@media screen and (min-width: 1024px) {
    .list--tick .list__item {
        padding-left: 0;
    }
}

/*icon*/
.icon {
    width: 40px;
    height: 40px;
}
.icon--small {
    width: 30px;
    height: 30px;
} 
.icon--primary {
    fill: var(--color-primary);
}
.icon--white {
    fill: #fff;
}
.icon-container {
    width: 64px;
    height: 64px;
    background: #f3f9fa;
    border-radius: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}
.icon-container--accent {
    background-color: var(--color-accent);
}

/*button*/
.btn {
    border-radius: 40px;
    cursor: pointer;
    white-space: nowrap;
    font-size: 1.8rem;
    font-weight: 600;
    margin-bottom: 2px;
    outline: 0;
    border: 0;
    text-align: center;
    text-transform: uppercase;
    padding: 2rem 4vw;
}

.btn .icon {
    width: 2rem;
    height: 2rem;
    margin-right: 1rem;
    vertical-align: middle;

}
.btn--primary {
    background: var(--color-primary);
    color: #fff;
}
.btn--primary:hover {
    background: #3a8ffd;
}
.btn--secondary {
    background: var(--color-secondary);
    color: #fff;
}
.btn--secondary:hover {
    background: #00c8eb;
}
.btn--accent {
    background: var(--color-accent);
    color: #fff;
}
.btn--accent:hover {
    background: #ec3000;
}
.btn--outline {
    color: var(--color-headings);
    background: #fff;
    border: 2px solid var(--color-headings);
}
.btn--outline:hover {
    background: var(--color-headings);
    color: #fff;
}
.btn--block {
    width: 100%;
    display:inline-block;
}
.btn--stretched {
    padding-left: 6rem;
    padding-right: 6rem;
}
@media screen and (min-width: 1024px) {
    .btn {
        font-size: 1.5rem;
    }
}

/*input*/
.input {
    padding: 1.5rem 3.5rem;
    border-radius: 30px;
    outline: 0;
    border: 1px solid var(--color-border);
    font-size: 2rem;
    color: var(--color-headings);
}
::placeholder {
    color: #cdcbd7;
}
.input-group {
    border: 1px solid var(--color-border);
    border-radius: var(--border-radius);
    display: flex;
    margin: 0 auto;
}
.input-group .input {
    border: 0;
    flex-grow: 1;
    padding: 1.5rem 2rem;
    width: 0;
}
.input-group .btn {
    margin: 4px;
}
@media screen and (min-width: 1024px) {
    .input {
      font-size: 1.5rem;
    }
  }
  /* Cards */
  .card {
    border-radius: 7px;
    box-shadow: 0 0 20px 10px #f3f3f3;
    overflow: hidden;
  }
  
  .card__header,
  .card__body {
    padding: 2rem 3rem;
  }
  
  .card--primary .card__header {
    background: var(--color-primary);
    color: #fff;
  }
  .card--primary .badges--primary {
      background: #126de4;
  }
  .card--secondary .badges--secondary {
      background: rgba(49, 186, 190, 0.267);
  }
  
  .card--secondary .card__header {
    background: var(--color-secondary);
    color: #fff;
  }

/* Plans */
.plan:hover {
    transform: scale(1.05);
}
.plan__name {
    color: #fff;
    margin: 0;
    font-weight: 500;
    font-size: 2.4rem;
  }
  
  .plan__price {
    font-size: 6rem;
  }
  
  .plan--popular:hover {
      transform: scale(1.15);
  }
  .plan__billing-cycle {
    font-size: 2.4rem;
    font-weight: 300;
    opacity: 0.8;
    margin-right: 1rem;
  }
  
  .plan__description {
    font-size: 2rem;
    font-weight: 300;
    letter-spacing: 1px;
    display: block;
  }
  
  .plan .list__item {
    margin-bottom: 2rem;
  }
  .plan--popular {
      transform: scale(1.1);
  }
  .plan--popular .card__header {
    position: relative;
  }
  
  .plan--popular .card__header::before {
    content: url(/images/popular.svg);
    width: 40px;
    display: inline-block;
    position: absolute;
    top: -6px;
    right: 5%;
    
  }
  
  @media screen and (min-width: 1024px) {
    .plan__name {
      font-size: 1.4rem;
    }
  
    .plan__price {
      font-size: 5rem;
    }
  
    .plan__billing-cycle {
      font-size: 1.6rem;
    }
  
    .plan__description {
      font-size: 1.7rem;
    }
  }

  /* Media */
.media {
    display: flex;
  }
  
  .media__title {
    margin-top: 0;
  }
  
  .media__body {
    margin: 0 2rem;
  }
  
  .media__image {
    margin-top: 1rem;
  }
  
 /* Quotes */
.quote {
    font-size: 3rem;
    font-style: italic;
    color: var(--color-body-darker);
    line-height: 1.3;
  }
  
  .quote__text::before {
    content: open-quote;
  }
  
  .quote__text::after {
    content: close-quote;
  }
  
  .quote__author {
    font-size: 3rem;
    font-weight: 500;
    font-style: normal;
    margin-bottom: 0;
  }
  
  .quote__organization {
    color: var(--color-headings);
    opacity: 0.4;
    font-size: 2rem;
    font-style: normal;
  }
  
  .quote__line {
    position: relative;
    bottom: 10px;
  }
  
  @media screen and (min-width: 1024px) {
    .quote {
      font-size: 2rem;
    }
  
    .quote__author {
      font-size: 2.4rem;
    }
  
    .quote__organization {
      font-size: 1.6rem;
    }
  }

/*grid*/
.grid {
    display: grid;
}

@media screen and (min-width: 768px) {
    .grid--1x2 {
        grid-template-columns: repeat(2, 1fr);
    }
    .grid--1x3 {
        grid-template-columns: repeat(3,1fr);
    }
}

/*testimonial*/
.testimonial {
    padding: 3rem;
}
.testimonial__image > img {
     width: 100%;
 }
.testimonial__image {
    position: relative;
}
.testimonial__icon {
    position: absolute;
    top: 3rem;
    right: -30px;
}


@media screen and (min-width: 768px) {
    .testimonial__quote .quote, 
    .testimonial__quote .quote__author {
        font-size: 2.4rem;

    }
    .testimonial__quote {
        margin-left: 3rem;
    }
}

/*Callout*/
.callout {
    padding: 4rem;
    border-radius: 5px;
}

.callout--primary {
    background: var(--color-primary);
    color: #fff;
}
.callout__heading {
    color: #fff;
    margin-top: 0;
    font-size: 3rem;
}
.callout .btn {
    justify-self: center;
    align-self: center;
}
.callout__content {
    text-align: center;
}
@media screen and (min-width: 768px) {
    .callout__content {
        text-align: start;
    }
    .callout .grid--1x2 {
        grid-template-columns: 1fr auto;
    }
    .callout .btn {
        margin-left: 0 2rem;
    }
}

/*collapsible*/
 .collapsible__header {
     display: flex;
     justify-content: space-between;
 }
 .collapsible__heading {
     margin-top: 0;
     font-size: 3rem;
 }
 .collapsible__chevron {
     transform: rotate(-90deg);
     transition: transform 0.3s;
 }
 .collapsible__content {
     max-height: 0;
     overflow: hidden;
     opacity: 0;
     transition: all 0.3s;

 }
 .collapsible--expanded .collapsible__chevron {
     transform: rotate(0);
 }
 .collapsible--expanded .collapsible__content {
     max-height: 100vh;
     opacity: 1;
 }

 /*block*/
 .block {
 --padding-vertical: 6rem;
 padding: var(--padding-vertical) 2rem;
 }
 
 .block__header {
     text-align: center;
     margin-bottom: 4rem;
 }
 .block__heading {
     margin-top: 0;
 }
 .block--dark {
     background: #000;
     color: #7b858b;
 }
 .block--dark h1,
 .block--dark h2,
 .block--dark h3 {
     color: #fff;
 }


 .block--dark .block__heading {
     color: #fff;
 }
 .block--skewed-right {
     padding-bottom: calc(var(--padding-vertical) + 4rem);
     clip-path: polygon(0 0, 100% 0, 100% 80%, 0% 100%);
 }
 .block--skewed-left {
    padding-bottom: calc(var(--padding-vertical) + 4rem);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 80%);
}
.block-showcase {
    margin-bottom: 10rem;
}
.container {
    max-width: 1140px;
    margin: 0 auto;
  }

/*navigation*/
.nav {
    display: flex;
    justify-content: space-between;
    background: #000;
    flex-wrap: wrap;
    padding:0 1rem;
    align-items: center;
}

.nav__list {
    width: 100%;
    margin: 0;
}
.nav__item {
    padding: 0.5rem 2rem;
    border-bottom: 1px solid #222;
}
.nav__item>a {
    color: #d2d0db;
    transition: color 0.3s;
}
.nav__item > a:hover {
    color: #fff;
}
.nav__toggler {
    opacity: 0.5;
    transition: box-shadow 0.15s;
    cursor: pointer;
}
.nav.collapsible--expanded .nav__toggler {
    opacity: 1;
    box-shadow: 0 0 0 3px #666;
    border-radius: 5px;
}
.nav__brand {
    transform: translateY(5px);
}

@media screen and (min-width: 768px) {
    .nav__toggler {
        display: none;
    }
    .nav__list {
        width: auto;
        display: flex;
        font-size: 1.6rem;
        max-height: 100%;
        opacity: 1;
    }
    .nav__item {
        border: 0;
    }
}

/*hero*/
.hero__image {
    width: 100%;
}
.hero {
    clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%); 
}
.hero__tagline {
    letter-spacing: 1px;
    font-size: 2rem;
    color: #b9c3cf;
    margin: 2rem 0 5rem;
}

@media screen and (min-width: 768px) {
    .hero__content {
        text-align: left;
        align-self: center;
    }
    .hero {
        padding-top: 0;
    }
}

/*DORMAIN BLOCK*/
.block-dormain .input-group {
    box-shadow: 0 0 30px 20px #e6ebee;
    border: 0;
    margin: 4rem auto;
    max-width: 722px;
}

.block-dormain__price {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 6rem);
    justify-items: center;
    font-size: 2rem;
    font-weight: 600;
    max-width: 722px;
    margin: 0 auto;
    color: var(--color-headings);
}

@media screen and (min-width: 768px) {
    .block-dormain__price {
        grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    }
    
}

/*block plan*/
.block-plans .grid {
    gap: 8rem 4rem;
}
.block-plans .card {
    max-width: 500px;
    margin: 0 auto;
}

/*feature block*/
.feature {
    gap: 4rem 2rem;
    align-items: start;
    justify-items: center;
    margin-top: 12rem;
}
.feature:first-of-type{
    margin-top: 6rem;
}
.feature__heading {
    margin-top: 1rem;
}
.feature__image {
    width: 100%;
}
@media screen and (min-width: 768px) {
    .feature:nth-of-type(even) .feature__content {
        order: 2;
    }   
}

/*Block SHOWCASE*/
.block-showcase__image img {
    width: 100%;
}
.block-showcase .media {
    margin-bottom: 2rem;
}
.block-showcase .media:first-of-type {
    margin-top: 2rem;
}
@media screen and (min-width: 768px) {
    .block-showcase .grid {
        grid-template-columns: 50% 50%;
      }
    .block-showcase__image {
        justify-self: end;
      }
      .block-showcase__image > img {
        width: auto;
        max-width: 700px;
      }
}

/*footer*/
.footer {
    background: #232323;
}
.footer a {
    color: #777;
    transition: color 0.3s;
}
.footer__section .list {
    margin: 0;
}
.footer a:hover {
    color: #fff;
}
.footer__section {
    padding: 2rem;
    border-bottom: 1px solid #393939;
}
.footer__heading {
    text-transform: uppercase;
    font-weight: 600;
}
.footer__brand {
    margin: 5rem;
    text-align: center;
}
.footer__brand img {
    width: 100%;
    max-width: 230px;
}
.footer__copyright {
    font-size: 2.1rem;
    color: #fff;
    opacity: 0.3;
}
@media screen and (min-width: 768px) {
    .footer__sections {
        grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    }
    .footer .collapsible__chevron {
        display: none;
    }
    .footer .collapsible__content {
        max-height: 100%;
        opacity: 1;
    }
    .footer__section {
        border-bottom: none;
    }
    .footer__brand {
        order: -1;
        margin-top: 1rem;
    }
    .footer__copyright{
        font-size: 1.5rem;
    }
    .footer__heading {
        font-size: 1.6rem;
    }
    
}